* {
  border: none;
  outline: none;
  margin: 0;
  padding: 0;
}

body {
  background-color: #fff8ec;
}

main {
  flex-direction: column;
  align-items: center;
  padding: 0 1rem;
  display: flex;
}

h1, h2, .buttons, .copyright {
  font-family: Inknut Antiqua, Trebuchet MS, Lucida Sans Unicode, Lucida Grande, Lucida Sans, Arial, sans-serif;
}

h1 {
  color: #b59136;
  letter-spacing: 1px;
  margin: 1rem;
  font-size: 2.75rem;
}

h2 {
  color: #005b4c;
  font-size: 1.8rem;
}

.row {
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  display: flex;
}

#header-data {
  width: 100%;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

#user {
  margin: 1.5rem;
}

.logo {
  flex-direction: row;
  align-items: center;
  display: flex;
}

form {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

#login-form {
  background-color: #fff;
  border-radius: .5rem;
  flex-direction: column;
  justify-content: center;
  margin: 1rem;
  padding: 2rem;
  display: flex;
  box-shadow: 0 3px 8px #4a4453b3;
}

#message-form {
  margin: 1.5rem;
}

input {
  height: 2rem;
  border: 2px solid #b59136;
  border-radius: .5rem;
  margin: 1.5rem;
  padding: .5rem 1.5rem;
  transition-duration: .3s;
  box-shadow: 0 3px 8px #4a4453b3;
}

#username, #password {
  width: 15rem;
}

#message {
  width: 40rem;
}

.buttons {
  color: #fff;
  width: 6rem;
  height: 2.5rem;
  background-color: #b59136;
  border-radius: .5rem;
  margin: 1.5rem;
  font-size: 1.06rem;
  transition-duration: .3s;
  box-shadow: 0 3px 8px #4a4453b3;
}

.buttons:hover {
  background-color: #604200;
}

#forums-section {
  width: 100%;
  flex-direction: row;
  display: flex;
}

#sidebar {
  width: 15%;
  margin-top: 10rem;
}

#sidebar a {
  color: #b59136;
  padding: .5rem 1rem;
  font-size: 1.5rem;
  text-decoration: none;
  display: block;
}

#sidebar a:hover {
  color: #604200;
}

#topics-section {
  width: 85%;
  flex-direction: column;
  align-items: center;
  display: flex;
}

#travel-div {
  display: flex;
}

#sport-div, #gaming-div {
  display: none;
}

.topics {
  width: 80%;
  min-height: 30rem;
  background-color: #fff;
  border: 2px solid #005b4c;
  border-radius: .5rem;
  flex-direction: column;
  align-items: center;
  margin: 1rem;
  padding: 1rem;
  display: flex;
  box-shadow: 0 3px 8px #4a4453b3;
}

.footer {
  width: 100%;
  height: 4.2rem;
  background-color: #b59136;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  margin-top: 3rem;
  font-size: 1.1rem;
  display: flex;
  box-shadow: 4px 0 8px #4a4453b3;
}

.copyright {
  color: #fff;
  padding: .5rem;
}

#chevron a {
  color: #fff;
  font-size: 2.3rem;
}

#chevron a:hover {
  text-shadow: 2px 3px 5px #4a4453b3;
}

